  validcedulaFormControl(
    control: AbstractControl
  ): { [key: string]: any } | null {
    const cedula: any = control.value;
    if (cedula) {
      if (cedula.length == 10) {
        const digito_region = cedula.substring(0, 2);

        if (digito_region >= 1 && digito_region <= 24) {
          const ultimo_digito = cedula.substring(9, 10);

          const pares =
            parseInt(cedula.substring(1, 2)) +
            parseInt(cedula.substring(3, 4)) +
            parseInt(cedula.substring(5, 6)) +
            parseInt(cedula.substring(7, 8));

          let numero1 = cedula.substring(0, 1);
          numero1 = numero1 * 2;
          if (numero1 > 9) {
            numero1 = numero1 - 9;
          }

          let numero3 = cedula.substring(2, 3);
          numero3 = numero3 * 2;
          if (numero3 > 9) {
            numero3 = numero3 - 9;
          }

          let numero5 = cedula.substring(4, 5);
          numero5 = numero5 * 2;
          if (numero5 > 9) {
            numero5 = numero5 - 9;
          }

          let numero7 = cedula.substring(6, 7);
          numero7 = numero7 * 2;
          if (numero7 > 9) {
            numero7 = numero7 - 9;
          }

          let numero9 = cedula.substring(8, 9);
          numero9 = numero9 * 2;
          if (numero9 > 9) {
            numero9 = numero9 - 9;
          }

          const impares = numero1 + numero3 + numero5 + numero7 + numero9;

          const suma_total = pares + impares;

          const primer_digito_suma = String(suma_total).substring(0, 1);

          const decena = (parseInt(primer_digito_suma) + 1) * 10;

          let digito_validador = decena - suma_total;

          if (digito_validador == 10) { const digito_validador = 0; }

          if (digito_validador == ultimo_digito) {
            return null;
          } else {
            return { validcedulaFormControl: true };
          }
        } else {
          return { validcedulaFormControl: true };
        }
      } else {
        return { validcedulaFormControl: true };
      }
    }
  }
  MayorAge(control: AbstractControl): { [key: string]: any } | null {
    const fecha: any = control.value;
    const dateToday = new Date();
    if (
      fecha <=
      new Date(dateToday.setUTCFullYear(dateToday.getUTCFullYear() - 18))
    ) {
      return null;
    } else {
      return { MayorAge: true };
    }
  }
  CamporMayorMenor1(campomayor: string, campomenor: string): ValidatorFn {
    return (AC: FormControl) => {
      const dataForm = AC.parent;
      if (!dataForm) { return null; }

      const activos = dataForm.get(campomayor).value;
      const pasivos = dataForm.get(campomenor).value;
      if (activos >= 1 && pasivos >= 1) {
        if (activos > pasivos) {
          dataForm.controls[campomayor].setErrors(null);
          dataForm.controls[campomenor].setErrors(null);
          return null;
        } else {
          dataForm.controls[campomayor].setErrors({ ActivoMenorPasivo: true });
          dataForm.controls[campomenor].setErrors({ ActivoMenorPasivo: true });
          return { ActivoMenorPasivo: true };
        }
      }
    };
  }

  CamporMayorMenor2(campomayor: string, campomenor: string): ValidatorFn {
    return (AC: FormControl) => {
      const dataForm = AC.parent;
      if (!dataForm) { return null; }

      this.isDepend
        ? (campomayor = 'imensualesDepend')
        : (campomayor = 'imensualesJubilado');

      const activos = dataForm.get(campomayor).value;
      const pasivos = dataForm.get(campomenor).value;
      if (activos >= 1 && pasivos >= 1) {
        if (activos > pasivos) {
          dataForm.controls[campomayor].setErrors(null);
          dataForm.controls[campomenor].setErrors(null);
          return null;
        } else {
          dataForm.controls[campomayor].setErrors({
            IngresosmenorEgresos: true,
          });
          dataForm.controls[campomenor].setErrors({
            IngresosmenorEgresos: true,
          });
          return { IngresosmenorEgresos: true };
        }
      }
    };
  }

  gps() {

    let b: any;
    let c: any;
    const a = this.Step2Form.Domicilio.Coordenadas.split(',');
    if (a.length > 0){
        b = a[0].trim();
        c = a[1].trim();
    }else{

      b = this.lng;
      c = this.lat;

    }
    const dialogRef = this.dialog.open(MapComponent, {
      data: {long: b, lat: c} });
    dialogRef.afterClosed().subscribe((res) => {
      if (!res) {
        return;
      }
    });
  }
