import {DatosAspiranteModel} from './datos-aspirante.model';

export class ReferenciaPersonalModel {
  Secuencia: number;
  Nombres: string;
  Parentesco: string;
  Direccion: string;
  TipoTelefono: string;
  Telefono: string;

  clear() {
    this.Secuencia = 0;
    this.Nombres = '';
    this.Parentesco = '';
    this.Direccion = '';
    this.TipoTelefono = '';
    this.Telefono = '';
  }
}
