import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { Subject } from 'rxjs';
import { Observable } from 'rxjs';
import { WebcamImage, WebcamInitError, WebcamUtil } from 'ngx-webcam';
import { HostListener } from '@angular/core';

import { BoardingComponent } from '../../boarding.component';
import { AuthRecognitionFacial } from 'src/app/core/boarding/_models/auth-facial-recognition.model';
import { FacialRecognitionService } from 'src/app/core/boarding/_services/facial-recognition.service';
import { DatosAspiranteModel } from 'src/app/core/boarding/_models/datos-aspirante.model';

import { MatDialog } from '@angular/material/dialog';
import { AlertBlockComponent } from 'src/app/views/partials/alert-block/alert-block.component';
import { AlertSpinnerComponent } from 'src/app/views/partials/alert-spinner/alert-spinner.component';

import { DeviceDetectorService } from 'ngx-device-detector';
import { FaceOkComponent } from 'src/app/views/partials/face-ok/face-ok.component';

@Component({
  selector: 'app-facial-recognition',
  templateUrl: './facial-recognition.component.html',
  styles: [],
})
export class FacialRecognitionComponent implements OnInit {
  @Output()
  public pictureTaken = new EventEmitter<WebcamImage>();
  public rcFailed = false;
  public cedulaFailed = false;
  public showWebcam = true;
  public showFooterWebcam = false;
  public finishprocess = true;
  public cameraFrontal = true;
  public uploadPhoto1: any;
  public uploadPhoto2: any;
  public bodyToken: AuthRecognitionFacial;
  public datosAspirantes: DatosAspiranteModel;
  public contadorBloqueo = 0;
  public compareSuccesfuly = false;
  public photoTaken = false;
  public falloReconocimientoFacial = false;
  public imgResultBeforeCompress: string;
  public imgResultAfterCompress: string;
  public haveWebCam = false;
  public allowCameraSwitch = true;
  public multipleWebcamsAvailable = false;
  public deviceId: string;
  public videoOptions: MediaTrackConstraints = {
    width: { ideal: 170, max: 2400 },
    height: { ideal: 160, max: 1800 },
  };
  public videoOptionsCedula: MediaTrackConstraints = {
    width: { ideal: 220, max: 2400 },
    height: { ideal: 120, max: 1800 },
  };
  public errors: WebcamInitError[] = [];
  public fileToUpload: File = null;

  private trigger: Subject<void> = new Subject<void>();

  private nextWebcam: Subject<boolean | string> = new Subject<
    boolean | string
  >();
  public IsMobile: boolean;

  error: WebcamInitError;

  screenHeight: number;
  screenWidth: number;
  widthCedCam: number;

  @HostListener('window:resize', ['$event'])
  getScreenSize(event?) {
    this.screenHeight = window.innerHeight;
    this.screenWidth = window.innerWidth - 50;

    window.innerWidth < 575
      ? (this.widthCedCam = this.screenWidth)
      : (this.widthCedCam = 450);
  }

  constructor(
    public father: BoardingComponent,
    private coreService: FacialRecognitionService,
    public dialog: MatDialog,
    private deviceService: DeviceDetectorService
  ) {
    this.getScreenSize();
  }

  async ngOnInit(): Promise<void> {
    console.log('iniciando');
    document.oncontextmenu = function() {
      return false;
    };
    if (location.protocol !== 'https:') {
      /* Reemplazar en produccion */
      console.log('https');
      this.IsMobile = this.deviceService.isMobile();
      console.log('si es un mobile');
      console.log(this.IsMobile);

      navigator.permissions.query({ name: 'camera' }).then((res) => {
        res.onchange = function cambiar() {
          if (res.state !== 'denied') {
            window.location.reload();
          }
        };
      });

      console.log('llegamos a tener camara');
      navigator.permissions
                  .query({ name: 'camera' })
                  .then(async (res) => {
                    if (res.state == 'granted') {
                      const dialogRef = this.dialog.open(
                        AlertSpinnerComponent,
                        {
                          data: {},
                        }
                      );
                      this.showFooterWebcam = true;
                      WebcamUtil.getAvailableVideoInputs().then(
                        (mediaDevices: MediaDeviceInfo[]) => {
                          this.multipleWebcamsAvailable =
                            mediaDevices && mediaDevices.length > 1;
                        }
                      );

                      this.nextWebcam.next(this.cameraFrontal);
                      this.datosAspirantes = this.father.getInfoAspirante();

                      await this.coreService
                        .getTokenRC()
                        .toPromise()
                        .then(async (value) => {
                          this.bodyToken = value;

                          this.uploadPhoto2 =
                            this.datosAspirantes.Fotos[0] !== undefined
                              ? this.datosAspirantes.Fotos[0].Imagen.length > 0
                                ? this.datosAspirantes.Fotos[0].Imagen
                                : undefined
                              : undefined;

                          if (this.uploadPhoto2 != undefined) {
                            this.uploadPhoto2 =
                              'data:image/jpeg;base64,' + this.uploadPhoto2;

                            const response = await fetch(this.uploadPhoto2);
                            const blob = await response.blob();
                            await this.coreService
                              .uploadPhoto(
                                this.bodyToken.token,
                                blob,
                                'Imagen2'
                              )
                              .then((response) => response.json())
                              .then(async (responseData) => {
                                this.uploadPhoto2 = responseData;
                                console.log(this.uploadPhoto2);
                              })
                              .catch(() => {});
                          }
                        });

                      dialogRef.close();
                    } else if (res.state == 'prompt') {
                      this.showFooterWebcam = false;
                      const dialogRef = this.dialog.open(
                        AlertSpinnerComponent,
                        {
                          data: { mensaje: 'Esperando Permisos' },
                        }
                      );
                    } else if (res.state == 'denied') {
                      this.dialog.closeAll();
                      const dialogRef = this.dialog.open(AlertBlockComponent, {
                        data: {
                          mensaje:
                            'Permisos Denegados, no se puede continuar con el proceso de apertura de cuenta.',
                        },
                      });
                      this.showFooterWebcam = false;
                    }
                  })
                  .catch(() => {});

    } else {
      const dialogRef = this.dialog.open(AlertBlockComponent, {
        data: {
          mensaje: 'Sitio no seguro',
        },
      });
      this.showFooterWebcam = false;
    }
  }

  public triggerSnapshot(): void {
    this.trigger.next();
  }

  public toggleWebcam(): void {
    this.showWebcam = !this.showWebcam;
  }

  public handleInitError(error: WebcamInitError): void {
    if (
      error.mediaStreamError &&
      error.mediaStreamError.name === 'NotAllowedError'
    ) {
      this.dialog.closeAll();
      const dialogRef = this.dialog.open(AlertBlockComponent, {
        data: {
          mensaje:
            'Permisos Denegados, no se puede continuar con el proceso de apertura de cuenta.',
        },
      });
      this.showFooterWebcam = false;
    }
    this.errors = [];
    this.errors.push(error);
  }

  public showNextWebcam(): void {
    this.cameraFrontal = !this.cameraFrontal;
    this.nextWebcam.next(this.cameraFrontal);
  }

  public async handleImage(webcamImage: WebcamImage): Promise<void> {
    this.photoTaken = true;
    this.falloReconocimientoFacial = false;

    this.pictureTaken.emit(webcamImage);
    const imageurl = webcamImage.imageAsDataUrl;

    const response = await fetch(imageurl);
    const blob = await response.blob();

    await this.coreService
      .uploadPhoto(
        this.bodyToken.token,
        blob,
        this.uploadPhoto1 === undefined ? 'Imagen1' : 'Imagen2'
      )
      .then((response) => response.json())
      .then(async (responseData) => {
        this.uploadPhoto1 === undefined
          ? (this.uploadPhoto1 = responseData)
          : (this.uploadPhoto2 = responseData);

        this.uploadPhoto2 === undefined
          ? (this.rcFailed = true)
          : (this.rcFailed = false);

        if (
          this.uploadPhoto1 !== undefined &&
          this.uploadPhoto2 !== undefined
        ) {
          await this.coreService
            .comparePhotos(
              this.uploadPhoto1.urlPhoto,
              this.uploadPhoto2.urlPhoto,
              this.bodyToken.token
            )
            .then((response) => response.json())
            .then(async (responseData) => {
              responseData.isIdentical
                ? responseData.confidence >= 0.8
                  ? (this.compareSuccesfuly = true)
                  : ((this.contadorBloqueo = this.contadorBloqueo + 1),
                    (this.compareSuccesfuly = false))
                : ((this.contadorBloqueo = this.contadorBloqueo + 1),
                  (this.compareSuccesfuly = false));

              this.uploadPhoto1 = undefined;
              this.uploadPhoto2 =
                this.datosAspirantes.Fotos[0] !== undefined
                  ? this.datosAspirantes.Fotos[0].Imagen.length > 0
                    ? this.datosAspirantes.Fotos[0].Imagen
                    : undefined
                  : undefined;

              if (this.uploadPhoto2 != undefined) {
                this.uploadPhoto2 =
                  'data:image/jpeg;base64,' + this.uploadPhoto2;
                const response = await fetch(this.uploadPhoto2);
                const blob = await response.blob();
                await this.coreService
                  .uploadPhoto(this.bodyToken.token, blob, 'Imagen2')
                  .then((response) => response.json())
                  .then(async (responseData) => {
                    this.uploadPhoto2 = responseData;
                  })
                  .catch(() => {});
              }

              this.compareSuccesfuly
                ? (this.alertSucces(),
                  (this.photoTaken = false),
                  this.Paso1Succesfull())
                : this.contadorBloqueo === 3
                ? ((this.photoTaken = false), this.Bloqueo())
                : (this.falloReconocimientoFacial = true);
            })
            .catch((errir) => {
              console.log(errir);
              this.contadorBloqueo = this.contadorBloqueo + 1;
              this.compareSuccesfuly = false;
            });
        }

        this.photoTaken = false;
      })
      .catch(() => {});
  }
  public Paso1Succesfull() {
    this.toggleWebcam();
    this.rcFailed = false;
    this.finishprocess = false;
  }
  public Bloqueo() {
    this.toggleWebcam();
    this.rcFailed = false;
    this.finishprocess = false;
    this.alertBloqueo();
  }
  alertBloqueo() {
    const dialogRef = this.dialog.open(AlertBlockComponent, {
      data: {
        mensaje: 'Ha excedido el número de intentos permitidos.',
      },
    });
  }

  alertSucces() {
    const dialogRef = this.dialog.open(FaceOkComponent, {
      data: {},
    });
    dialogRef.afterClosed().subscribe((res) => {
      this.father.nextTab();
      if (!res) {
        return;
      }
    });
  }

  public cameraWasSwitched(deviceId: string): void {
    this.deviceId = deviceId;
  }

  public get triggerObservable(): Observable<void> {
    return this.trigger.asObservable();
  }

  public get nextWebcamObservable(): Observable<boolean | string> {
    return this.nextWebcam.asObservable();
  }

  public reconocimientoFacial() {
    this.triggerSnapshot();
  }
  public validacionCedula() {
    this.triggerSnapshot();
  }
}
