import { Component, OnInit } from '@angular/core';
import { BoardingComponent } from '../../boarding.component';
import { ContractComponent } from '../../../../partials/contract/contract.component';
import { MatDialog } from '@angular/material/dialog';
import { ValidarAspiranteResponseModel } from 'src/app/core/boarding/_models/validarAspiranteResponse.model';

@Component({
  selector: 'app-confirmation',
  templateUrl: './confirmation.component.html',
  styles: [
  ]
})
export class ConfirmationComponent implements OnInit {

  nombreCliente: string;
  cedulaCliente: string;
  correoCliente: string;
  lugarEntregaTD: string;


  constructor(
    public dialog: MatDialog,
    public father: BoardingComponent
  ) { }

  ngOnInit(): void {
    const datosAspirante = this.father.getInfoAspirante();    
    this.nombreCliente = datosAspirante.Nombres + '' + datosAspirante.Apellidos;
    this.cedulaCliente = datosAspirante.Cedula;
    this.correoCliente = datosAspirante.CorreoElectronico;
    //this.lugarEntregaTD = datosAspirante.EntregaTarjeta.Direccion;    
  }

  contract() {
    console.log(this.father.getInfoAspirante());
    const dialogRef = this.dialog.open(ContractComponent,
      {
        maxHeight: '90vh',
        maxWidth: '90vw',
      });
    dialogRef.afterClosed().subscribe(res => {
      if (!res) {
        return;
      }
    });
  }

}
