import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { BackReplyModel } from 'src/app/core/boarding/_models/back-reply.model';
import { EntregaTarjetaModel } from 'src/app/core/boarding/_models/entrega-tarjeta.model';
import { OficinasModel } from 'src/app/core/boarding/_models/oficinas.model';
import { ParametrosInicialesResponse } from 'src/app/core/boarding/_models/parametros-inicialesResponse.model';
import { AgencySelectionService } from 'src/app/core/boarding/_services/agency-selection.service';
import { ApplicantInformationService } from 'src/app/core/boarding/_services/applicant-information.service';
import { ParametersService } from 'src/app/core/boarding/_services/parameters.service';
import { BoardingComponent } from '../../boarding.component';

@Component({
  selector: 'app-agency-selection',
  templateUrl: './agency-selection.component.html',
  styles: [
  ]
})
export class AgencySelectionComponent implements OnInit {
    
  entregaTarjeta: EntregaTarjetaModel;
  parametrosIniciales: ParametrosInicialesResponse;
  provinces: BackReplyModel;
  cantonDomicilio: BackReplyModel;
  cantonDomOficina: BackReplyModel;
  cantonOficina: BackReplyModel;
  oficinaAperturaCuenta: OficinasModel;
  oficinaEntrega: OficinasModel;
  formGroupDomicilio: FormGroup;
  formGroupOficina: FormGroup;
  sectionFormDomicilio = true;
  sectionFormOficina = false;
  servicioEntregaDomicilio: boolean;
  msgAlertTitle = 'Selecciona la oficina de Entrega de Tarjeta de Débito más cercana para usted';
  body = {
    Cliente: "POSTMAN",
    Token: "123",
    IdAcceso: "10.150.0.142",
    UsuarioFinal: "PRUEBA LOCAL"
  };
  bodyAl = {
    Auditoria: {
      Cliente: "SOAP UI",
      Token: "12345",
      IdAcceso: "10.150.0.142",
      UsuarioFinal: "PRUEBA LOCAL"
    },
    CodigoProvincia: "1"
  };
  bodyAl2 = {
    Auditoria: {
      Cliente: "SOAP UI",
      Token: "12345",
      IdAcceso: "10.150.0.142",
      UsuarioFinal: "PRUEBA LOCAL"
    },
    CodigoProvincia: "1",
    CodigoCanton: "1",
    CodigoParroquia: "0"
  };

  constructor(
    private fb: FormBuilder,
    public father: BoardingComponent,
    private coreService: ApplicantInformationService,
    private service: AgencySelectionService,
    private paramsService: ParametersService
  ) {
    this.formGroupDomicilio = fb.group({
      provincia: [{ value: '', disabled: false }, [Validators.required]],
      canton: [{ value: '', disabled: false }, [Validators.required]],
      dirDomicilio: [{ value: '', disabled: false }, [Validators.required]],
      provinciaOficina: [{ value: '', disabled: false }, [Validators.required]],
      cantonOficina: [{ value: '', disabled: false }, [Validators.required]],
      oficina: [{ value: '', disabled: false }, [Validators.required]]
    });
    this.formGroupOficina = fb.group({
      provinciaOficina: [{ value: '', disabled: false }, [Validators.required]],
      cantonOficina: [{ value: '', disabled: false }, [Validators.required]],
      oficina: [{ value: '', disabled: false }, [Validators.required]],
      dirOficina: [{ value: '', disabled: false }, [Validators.required]]
    });
  }

  async ngOnInit() {    
    this.setValueOfficeDomicilio();
    this.parametrosIniciales = await this.paramsService.GetInitialParameters(this.body).toPromise();
    this.servicioEntregaDomicilio = this.parametrosIniciales.ServicioEntregaDomicilio;
    // Descomentar para pruebas
    /* this.servicioEntregaDomicilio = true;  */   
    this.entregaTarjeta = new EntregaTarjetaModel();
    this.entregaTarjeta.clear();
    this.provinces = await this.coreService.ConsultaProvincias(this.body).toPromise();
  }

  async setValueOfficeDomicilio() {
    const datosAspirante = this.father.getInfoAspirante();      
    this.formGroupDomicilio.get('dirDomicilio').setValue(datosAspirante.Domicilio.Direccion);    
    this.formGroupDomicilio.get('provincia').setValue(datosAspirante.Domicilio.CodProvincia);
    this.formGroupOficina.get('provinciaOficina').setValue(datosAspirante.Domicilio.CodProvincia);
    this.bodyAl.CodigoProvincia = this.formGroupDomicilio.get('provincia').value;
    const listaCanton = await this.coreService.ConsultaCantones(this.bodyAl).toPromise();    
    this.cantonDomicilio = listaCanton;
    this.cantonOficina = listaCanton;
    this.formGroupDomicilio.get('canton').setValue(datosAspirante.Domicilio.CodCanton);
    this.formGroupOficina.get('cantonOficina').setValue(datosAspirante.Domicilio.CodCanton);
    this.bodyAl2.CodigoProvincia = this.formGroupOficina.get('provinciaOficina').value;
    this.bodyAl2.CodigoCanton = this.formGroupOficina.get('cantonOficina').value;
    this.oficinaEntrega = await this.service.ConsultaOficinas(this.bodyAl2).toPromise();
  }

  changeRadio(event) {    
    const value = event.value;
    switch (value) {
      case 'Domicilio':
        this.sectionFormDomicilio = false;
        this.sectionFormOficina = true;
        this.msgAlertTitle = 'Escoja el lugar de entrega de la tarjeta';
        break;
      case 'Oficina':
        this.sectionFormDomicilio = true;
        this.sectionFormOficina = false;
        this.msgAlertTitle = 'Selecciona la oficina de Entrega de Tarjeta de Débito más cercana para ti';
        break;
      default:
        this.sectionFormDomicilio = false;
        this.sectionFormOficina = true;
        this.msgAlertTitle = 'Escoja el lugar de entrega de la tarjeta';
        break;
    }
  }

  /* Combos de sección Domicilio */
  async changeProvince() {
    this.bodyAl.CodigoProvincia = this.formGroupDomicilio.get('provincia').value;
    this.cantonDomicilio = await this.coreService.ConsultaCantones(this.bodyAl).toPromise();
    console.log(this.formGroupDomicilio.get('provincia').value);
  }

  async changeProvinceOficina() {
    this.bodyAl.CodigoProvincia = this.formGroupDomicilio.get('provinciaOficina').value;
    this.cantonDomOficina = await this.coreService.ConsultaCantones(this.bodyAl).toPromise();
    console.log(this.formGroupDomicilio.get('provinciaOficina').value);
  }

  async changeCantonOficina() {
    this.bodyAl2.CodigoProvincia = this.formGroupDomicilio.get('provinciaOficina').value;
    this.bodyAl2.CodigoCanton = this.formGroupDomicilio.get('cantonOficina').value;
    this.oficinaAperturaCuenta = await this.service.ConsultaOficinas(this.bodyAl2).toPromise();
  }
  /*  */

  /* Combos de sección Oficina */
  async changeProvinceOffice() {
    this.bodyAl.CodigoProvincia = this.formGroupOficina.get('provinciaOficina').value;
    this.cantonOficina = await this.coreService.ConsultaCantones(this.bodyAl).toPromise();
    console.log(this.formGroupOficina.get('provinciaOficina').value);
  }

  async changeCantonOffice() {
    this.bodyAl2.CodigoProvincia = this.formGroupOficina.get('provinciaOficina').value;
    this.bodyAl2.CodigoCanton = this.formGroupOficina.get('cantonOficina').value;
    this.oficinaEntrega = await this.service.ConsultaOficinas(this.bodyAl2).toPromise();
  }
  /*  */

  obtenerCoordenadasOficina(valor, flag) {
    let oficinas: any;
    let resultado: any;
    switch (flag) {
      case 'D':
        oficinas = this.oficinaAperturaCuenta;
        oficinas.forEach(element => {
          if (element.Codigo === valor) {
            resultado = element.Latitud + ', ' + element.Longitud;
          }
        });
        break;
      case 'O':
        oficinas = this.oficinaEntrega;
        oficinas.forEach(element => {
          if (element.Codigo === valor) {
            resultado = element.Latitud + ', ' + element.Longitud;
          }
        });
        break;
    } 
    return resultado; 
  }

  continuar() {
    if(this.sectionFormDomicilio) {
      // Ingresa Oficina
      this.entregaTarjeta.LugarEntregaTarjeta = 'O';
      this.entregaTarjeta.Oficina = this.formGroupOficina.get('oficina').value;
      this.entregaTarjeta.Provincia = this.formGroupOficina.get('provinciaOficina').value;
      this.entregaTarjeta.Canton = this.formGroupOficina.get('cantonOficina').value;
      this.entregaTarjeta.Direccion = this.formGroupOficina.get('dirOficina').value;
      this.entregaTarjeta.Coordenadas = this.obtenerCoordenadasOficina(this.formGroupOficina.get('oficina').value, 'O');
      this.father.setEntregaTarjeta(this.entregaTarjeta);
    } else {
      // Ingresa Domicilio
      this.entregaTarjeta.LugarEntregaTarjeta = 'D';
      this.entregaTarjeta.Oficina = this.formGroupDomicilio.get('oficina').value;
      this.entregaTarjeta.Provincia = this.formGroupDomicilio.get('provincia').value;
      this.entregaTarjeta.Canton = this.formGroupDomicilio.get('canton').value;
      this.entregaTarjeta.Direccion = this.formGroupDomicilio.get('dirDomicilio').value;
      this.entregaTarjeta.Coordenadas = this.obtenerCoordenadasOficina(this.formGroupDomicilio.get('oficina').value, 'D');
      this.father.setEntregaTarjeta(this.entregaTarjeta);
    }    
  }
}
