import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Injectable } from '@angular/core';
import { AuthRecognitionFacial } from '../_models/auth-facial-recognition.model';

@Injectable()
export class FacialRecognitionService {
  private readonly apiUrl = 'http://192.168.33.12:8085';

  private readonly photoUpload: string = '/ImageUpload';
  private readonly getToken: string = '/Authentication';
  private readonly comparebyURls: string = '/FaceCompareByUrls';

  constructor(private http: HttpClient) {}

  private get urlphotoUpload() {
    return this.apiUrl + this.photoUpload;
  }

  private get urlgetToken() {
    return this.apiUrl + this.getToken;
  }
  private get compareFace() {
    return this.apiUrl + this.comparebyURls;
  }

  async uploadPhoto(token, blob, filename) {
    const myHeaders = new Headers();
    myHeaders.append('Authorization', `Bearer ${token}`);

    const file = new File([blob], `/${filename}.jpg`, { type: blob.type });
    console.log(file);
    const formdata1 = new FormData();
    formdata1.append('formFile', file);

    const requestOptions: RequestInit = {
      method: 'POST',
      headers: myHeaders,
      body: formdata1,
      redirect: 'follow',
    };
    const endpointUrl = `${this.urlphotoUpload}`;

    return fetch(endpointUrl, requestOptions);
  }
  getTokenRC(): Observable<AuthRecognitionFacial> {
    const endpointUrl = `${this.urlgetToken}`;
    return this.http.get<AuthRecognitionFacial>(endpointUrl);
  }

  comparePhotos(imagen1: string, imagen2: string, token: string) {
    const myHeaders = new Headers();
    myHeaders.append('Authorization', `Bearer ${token}`);
    myHeaders.append('Content-Type', 'application/json');

    var raw = JSON.stringify({
      Auditoria: {
        Cliente: 'SOAP UI',
        Token: '12345',
        IdAcceso: '10.150.0.142',
        TipoAcceso: 0,
        UsuarioFinal: 'PRUEBA LOCAL',
      },
      CodigoRelacionLaboral: 1,
    });
    var requestOptions: RequestInit = {
      method: 'POST',
      headers: myHeaders,
      body: raw,
      redirect: 'follow',
    };

    const endpointUrl = `${this.compareFace}`;
    return fetch(
      ` ${endpointUrl}?IMAGE_URL1=${imagen1}&IMAGE_URL2=${imagen2}`,
      requestOptions
    );
  }
}
