import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Injectable } from '@angular/core';
import { OficinasModel } from '../_models/oficinas.model';

@Injectable()
export class AgencySelectionService {

    private readonly apiUrl = 'http://192.168.33.12:81';

    private readonly oficinas: string = '/api/ConOficinas';

    constructor(
        private http: HttpClient
    ) { }

    private get urlOficinas() {
        return this.apiUrl + this.oficinas;
    }

    ConsultaOficinas(body: any): Observable<OficinasModel> {
        const endpointUrl = `${this.urlOficinas}`;
        return this.http.post<OficinasModel>(endpointUrl, body);
    }
}

