import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { BoardingComponent } from '../../boarding.component';
import { HttpClient } from '@angular/common/http';
import { ReferencesService } from '../../../../../core/boarding/_services/references.service';
import { DatosAspiranteModel } from 'src/app/core/boarding/_models/datos-aspirante.model';
import { ReferenciaPersonalModel } from 'src/app/core/boarding/_models/referencia-personal.model';
import { ReferenciaBancariaModel } from 'src/app/core/boarding/_models/referencia-bancaria.model';
import { AuditService } from 'src/app/core/_base/services/audit.service';
import { AuditModel } from 'src/app/core/_base/models/audit.model';
import { ParametrosInicialesResponse } from 'src/app/core/boarding/_models/parametros-inicialesResponse.model';
import { ParametersService } from 'src/app/core/boarding/_services/parameters.service';
import { ConfigService } from 'src/app/core/_base/services/config.service';

@Component({
  selector: 'app-references',
  templateUrl: './references.component.html',
  styles: [
  ]
})
export class ReferencesComponent implements OnInit {

  refPersMO: ReferenciaPersonalModel;
  refBankMO: ReferenciaBancariaModel;
  datosAspirante: DatosAspiranteModel;
  parametrosIniciales: ParametrosInicialesResponse;
  auditModel: AuditModel;
  hideReference = false;
  formGroupPersonalRef: FormGroup;
  formGroupBankRef: FormGroup;
  phonesType: any;
  financialEntities: any;
  productsType: any;
  listPersonalRefs = [];
  listBankRefs = [];
  sectionformPersonalRef = false;
  sectionformBankRef = false;
  sectionAddPersonalRef = true;
  sectionAddBankRef = true;
  alertaErrorRefPersonales = true;
  alertaErrorRefBank = true;
  numberPersonalRef: number;
  numberBankRef: number;
  body = {
    Cliente: "POSTMAN",
    Token: "123",
    IdAcceso: "10.150.0.142",
    UsuarioFinal: "PRUEBA LOCAL"
  };
  flagPanelRefPersonal = false;
  flagPanelRefBank = false;
  length = '10';

  constructor(
    private fb: FormBuilder,
    public father: BoardingComponent,
    private http: HttpClient,
    private coreService: ReferencesService,
    private audit: AuditService,
    private paramsService: ParametersService,
    public configS: ConfigService
  ) {
    this.formGroupPersonalRef = fb.group({
      Nombres: [{ value: '', disabled: false }, [Validators.required, Validators.pattern('^[a-zA-Z ]+$')]],
      Telefono: [{ value: '', disabled: false }, [Validators.required, Validators.pattern('^[0-9]+$')]],
      TipoTelefono: [{ value: '', disabled: false }, [Validators.required]]
    });
    this.formGroupBankRef = fb.group({
      tipoEntidadFinanciera: [{ value: configS.getValue('TIPO_ENTIDAD'), disabled: false }, [Validators.required]],
      TipoProducto: [{ value: '', disabled: false }, [Validators.required]],
      EntidadFinanciera: [{ value: '', disabled: false }, [Validators.required]],
      NumeroCuenta: [{ value: '', disabled: false }, [Validators.required, Validators.pattern('^[0-9]+$'), Validators.minLength(16)]]
    });
  }

  async ngOnInit() {
    this.parametrosIniciales = await this.paramsService.GetInitialParameters(this.body).toPromise();
    this.numberPersonalRef = this.parametrosIniciales.MinimoReferenciasPersonales;
    this.numberBankRef = this.parametrosIniciales.MinimoReferenciasBancarias;
    this.datosAspirante = new DatosAspiranteModel();
    this.datosAspirante.clear();
    this.auditModel = new AuditModel();
    this.auditModel.clear();
    this.auditModel = this.audit.getAudit();
    this.phonesType = await this.coreService.ConsultaTiposTelefono(this.body).toPromise();
    this.financialEntities = await this.coreService.ConsultaEntidadesFinancieras(this.body).toPromise();
    this.productsType = await this.coreService.ConsultaTiposProductos(this.body).toPromise();
    this.setReferencesIfExist(true);
  }

  setReferencesIfExist(flag) {
    const aspirante = this.father.getInfoAspirante();
    /* aspirante.ReferenciasPersonales = [];
    aspirante.ReferenciasBancarias = []; */
    if (aspirante.ReferenciasPersonales.length > 0 && flag) {
      this.listPersonalRefs = aspirante.ReferenciasPersonales;
      this.flagPanelRefPersonal = true;
      this.sectionAddPersonalRef = false;
      this.sectionformPersonalRef = true;
    }
    if (aspirante.ReferenciasBancarias.length > 0) {
      this.listBankRefs = aspirante.ReferenciasBancarias;
      this.flagPanelRefBank = true;
      this.sectionAddBankRef = false;
      this.sectionformBankRef = true;
    }
  }

  changeRadio(event) {
    const value = event.value;
    switch (value) {
      case 'SI':
        this.hideReference = false;
        this.setReferencesIfExist(false);
        break;
      case 'NO':
        this.hideReference = true;
        this.listBankRefs = [];
        break;
      default:
        this.hideReference = false;
        break;
    }
  }

  changePhoneType(event) {
    const value = event.value;
    switch (value) {
      case '01':
        this.formGroupPersonalRef.get('Telefono').setValidators([Validators.required, Validators.pattern('^[0-9]+$'), Validators.minLength(9)]);
        this.length = '9';
        break;
      case '02':
        this.formGroupPersonalRef.get('Telefono').setValidators([Validators.required, Validators.pattern('^[0-9]+$'), Validators.minLength(10)]);
        this.length = '10';
        break;
      default:
        break;
    }
    this.formGroupPersonalRef.get('Telefono').reset();
  }

  addRef(tipo: number) {
    switch (tipo) {
      case 0:
        if (sessionStorage.getItem('EditIndex')) {
          const index = parseInt(sessionStorage.getItem('EditIndex'));
          this.listPersonalRefs[index] = {
            Nombres: this.formGroupPersonalRef.get('Nombres').value,
            Telefono: this.formGroupPersonalRef.get('Telefono').value,
            TipoTelefono: this.formGroupPersonalRef.get('TipoTelefono').value
          };
          sessionStorage.removeItem('EditIndex');
        } else {
          this.listPersonalRefs.push({
            Nombres: this.formGroupPersonalRef.get('Nombres').value,
            Telefono: this.formGroupPersonalRef.get('Telefono').value,
            TipoTelefono: this.formGroupPersonalRef.get('TipoTelefono').value
          });
        }
        this.sectionformPersonalRef = true;
        this.sectionAddPersonalRef = false;
        this.formGroupPersonalRef.reset();
        break;
      case 1:
        if (sessionStorage.getItem('EditIndex')) {
          const index = parseInt(sessionStorage.getItem('EditIndex'));
          this.listBankRefs[index] = {
            tipEntFin: this.configS.getValue('TIPO_ENTIDAD'),
            EntidadFinanciera: this.formGroupBankRef.get('EntidadFinanciera').value,
            TipoProducto: this.formGroupBankRef.get('TipoProducto').value,
            NumeroCuenta: this.formGroupBankRef.get('NumeroCuenta').value
          };
          sessionStorage.removeItem('EditIndex');
        } else {
          this.listBankRefs.push({
            tipEntFin: this.configS.getValue('TIPO_ENTIDAD'),
            EntidadFinanciera: this.formGroupBankRef.get('EntidadFinanciera').value,
            TipoProducto: this.formGroupBankRef.get('TipoProducto').value,
            NumeroCuenta: this.formGroupBankRef.get('NumeroCuenta').value
          });
        }
        this.sectionformBankRef = true;
        this.sectionAddBankRef = false;
        this.formGroupBankRef.reset();
        break;
      default:
        break;
    }
  }

  goAddRef(tipo: number) {
    switch (tipo) {
      case 0:
        this.sectionformPersonalRef = false;
        this.sectionAddPersonalRef = true;
        this.formGroupPersonalRef.reset();
        if (sessionStorage.getItem('EditIndex')) {
          const index = parseInt(sessionStorage.getItem('EditIndex'));
          this.formGroupPersonalRef.get('Nombres').setValue(this.listPersonalRefs[index].Nombres);
          this.formGroupPersonalRef.get('Telefono').setValue(this.listPersonalRefs[index].Telefono);
          this.formGroupPersonalRef.get('TipoTelefono').setValue(this.listPersonalRefs[index].TipoTelefono);
        }
        break;
      case 1:
        this.sectionformBankRef = false;
        this.sectionAddBankRef = true;
        this.formGroupBankRef.reset();
        this.formGroupBankRef.get('tipoEntidadFinanciera').setValue(this.configS.getValue('TIPO_ENTIDAD'));
        if (sessionStorage.getItem('EditIndex')) {
          const index = parseInt(sessionStorage.getItem('EditIndex'));          
          this.formGroupBankRef.get('EntidadFinanciera').setValue(this.listBankRefs[index].EntidadFinanciera);
          this.formGroupBankRef.get('TipoProducto').setValue(this.listBankRefs[index].TipoProducto);
          this.formGroupBankRef.get('NumeroCuenta').setValue(this.listBankRefs[index].NumeroCuenta);
        }
        break;
      default:
        break;
    }
  }

  togglePanelRef(id: number) {
    switch (id) {
      case 0:
        this.flagPanelRefPersonal = !this.flagPanelRefPersonal;
        break;
      case 1:
        this.flagPanelRefBank = !this.flagPanelRefBank;
        break;
      default:
        break;
    }
  }

  closeRef(id: number) {
    sessionStorage.removeItem('EditIndex');
    switch (id) {
      case 0:
        if (this.listPersonalRefs.length > 0) {
          this.sectionformPersonalRef = true;
          this.sectionAddPersonalRef = false;
        } else {
          this.flagPanelRefPersonal = false;
        }
        break;
      case 1:
        if (this.listBankRefs.length > 0) {
          this.sectionformBankRef = true;
          this.sectionAddBankRef = false;
        } else {
          this.flagPanelRefBank = false;
        }
        break;
      default:
        break;
    }
  }

  editReference(i: number, tipo: number) {
    sessionStorage.setItem('EditIndex', i.toString());
    switch (tipo) {
      case 0:
        this.goAddRef(0);
        break;
      case 1:
        this.goAddRef(1);
        break;
      default:
        break;
    }
  }

  deleteReference(i: number, tipo: number) {
    switch (tipo) {
      case 0:
        this.listPersonalRefs.splice(i, 1);
        break;
      case 1:
        this.listBankRefs.splice(i, 1);
        break;
      default:
        break;
    }
  }

  getProductTypeName(value) {
    let varRetorno = '';
    this.productsType.forEach(element => {
      if (element.Codigo.toString() === value.toString()) {
        varRetorno = element.Descripcion;
      }
    });
    return varRetorno;
  }

  getFinancialEntityName(value) {
    let varRetorno = '';
    this.financialEntities.forEach(element => {
      if (element.Codigo.toString() === value.toString()) {
        varRetorno = element.Descripcion;
      }
    });
    return varRetorno;
  }

  getProductTypeId(desc) {
    let varRetorno = '';
    this.productsType.forEach(element => {
      if (element.Descripcion.toString() === desc.toString()) {
        varRetorno = element.Codigo;
      }
    });
    return varRetorno;
  }

  getFinancialEntityId(desc) {
    let varRetorno = '';
    this.financialEntities.forEach(element => {
      if (element.Descripcion.toString() === desc.toString()) {
        varRetorno = element.Codigo;
      }
    });
    return varRetorno;
  }

  continuar() {
    if (this.listPersonalRefs.length < this.numberPersonalRef || this.listBankRefs.length < this.numberBankRef) {
      if (this.listPersonalRefs.length < this.numberPersonalRef) {
        this.alertaErrorRefPersonales = false;
      } else {
        this.alertaErrorRefPersonales = true;
      }
      if (this.listBankRefs.length < this.numberBankRef) {
        this.alertaErrorRefBank = false;
      } else {
        this.alertaErrorRefBank = true;
      }
      return;
    }
    this.alertaErrorRefPersonales = true;
    this.alertaErrorRefBank = true;
    // Aquí se llena el JSON de CrearCuentaDigital
    // LLenado Referencia Personal
    if (this.listPersonalRefs.length > 0) {
      this.listPersonalRefs.forEach(element => {
        this.refPersMO = new ReferenciaPersonalModel();
        this.refPersMO.clear();
        this.refPersMO.Nombres = element.Nombres;
        this.refPersMO.TipoTelefono = element.TipoTelefono;
        this.refPersMO.Telefono = element.Telefono;
        this.datosAspirante.ReferenciasPersonales.push(this.refPersMO);
      });
    }
    // LLenado Referencia Bancaria
    if (this.listBankRefs.length > 0) {
      this.listBankRefs.forEach(element => {
        this.refBankMO = new ReferenciaBancariaModel();
        this.refBankMO.clear();
        this.refBankMO.EntidadFinanciera = element.EntidadFinanciera;
        this.refBankMO.TipoProducto = element.TipoProducto;
        this.refBankMO.NumeroCuenta = element.NumeroCuenta;
        this.datosAspirante.ReferenciasBancarias.push(this.refBankMO);
      });
    }
    this.datosAspirante.TieneCuentasSistemaFinancieroNacional = !this.hideReference;
    this.father.setReferenciasPersonales(this.datosAspirante.ReferenciasPersonales);
    this.father.setReferenciasBancarias(this.datosAspirante.ReferenciasBancarias);
    this.father.TieneCuentasSistemaFinancieroNacional(this.datosAspirante.TieneCuentasSistemaFinancieroNacional);
    console.log(this.father.getInfoAspirante());
    this.datosAspirante.clear();
    this.father.nextTab();
  }

  validateKey(evt) {
    var code = (evt.which) ? evt.which : evt.keyCode;
    if (code == 8) {
      return true;
    } else if (code >= 48 && code <= 57) {
      return true;
    } else {
      return false;
    }
  }

  cerrarAlertaError(id) {
    switch (id) {
      case 0:
        this.alertaErrorRefPersonales = true;
        break;
      case 1:
        this.alertaErrorRefBank = true;
        break;
    }
  }
}
