import { Injectable } from '@angular/core';
import { Resolve, Router, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';
import { Observable, of, EMPTY } from 'rxjs';

import { mergeMap, take } from 'rxjs/operators';
import {ParametersService} from '../parameters.service';
import {ConfigService} from '../../../_base/services/config.service';

@Injectable({
  providedIn: 'root'
})
export class BoardingResolverService implements Resolve<any> {
  constructor (private router: Router) { }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any> | Observable<never> {

    if (sessionStorage.getItem('ValidaAspirante') !== null){
      const guardado = sessionStorage.getItem('ValidaAspirante');
      // sessionStorage.removeItem('ValidaAspirante');
      return of(JSON.parse(guardado));
    }else{
      this.router.navigate(['/']);
    }
  }
}
