import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { BoardingComponent } from './boarding.component';
import { AgencySelectionComponent } from './_subs/agency-selection/agency-selection.component';
import { ApplicantInformationComponent } from './_subs/applicant-information/applicant-information.component';
import { ConfirmationComponent } from './_subs/confirmation/confirmation.component';
import { FacialRecognitionComponent } from './_subs/facial-recognition/facial-recognition.component';
import { ReferencesComponent } from './_subs/references/references.component';
import { RouterModule } from '@angular/router';
import { MatTabsModule } from '@angular/material/tabs';
import { MatIconModule } from '@angular/material/icon';
import { ReactiveFormsModule } from '@angular/forms';
import { MatButtonModule } from '@angular/material/button';
import { MatInputModule } from '@angular/material/input';
import { MatSelectModule } from '@angular/material/select';
import { MatExpansionModule } from '@angular/material/expansion';
import { PartialsModule } from '../../partials/partials.module';
import { WebcamModule } from 'ngx-webcam';
import { MatRadioModule } from '@angular/material/radio';
import { MatDatepickerModule } from '@angular/material/datepicker';
import { MatNativeDateModule } from '@angular/material/core';
import { HttpClientModule } from '@angular/common/http';
import { ApplicantInformationService } from '../../../core/boarding/_services/applicant-information.service';
import { ReferencesService } from '../../../core/boarding/_services/references.service';
import { AgencySelectionService } from 'src/app/core/boarding/_services/agency-selection.service';
import { FacialRecognitionService } from '../../../core/boarding/_services/facial-recognition.service';
import { BoardingResolverService } from '../../../core/boarding/_services/_resolvers/boarding-resolver.service';
import { CurrencyMaskInputMode, NgxCurrencyModule } from 'ngx-currency';
import { MatProgressSpinnerModule } from '@angular/material/progress-spinner';

export const customCurrencyMaskConfig = {
  allowNegative: false,
  allowZero: false,
  precision: 2,
  suffix: '',
  min: null,
  max: null,
  prefix: '$',
  thousands: '.',
  decimal: ',',
  nullable: true,
  align: 'left',
  inputMode: CurrencyMaskInputMode.FINANCIAL,
};

@NgModule({
  declarations: [
    BoardingComponent,
    AgencySelectionComponent,
    ApplicantInformationComponent,
    ConfirmationComponent,
    FacialRecognitionComponent,
    ReferencesComponent,
  ],
  imports: [
    CommonModule,
    RouterModule.forChild([
      {
        path: '',
        component: BoardingComponent,
        resolve: {
          info: BoardingResolverService,
        },
      },
    ]),
    MatTabsModule,
    MatIconModule,
    MatInputModule,
    MatSelectModule,
    ReactiveFormsModule,
    MatButtonModule,
    MatExpansionModule,
    PartialsModule,
    WebcamModule,
    MatRadioModule,
    MatDatepickerModule,
    MatNativeDateModule,
    HttpClientModule,
    MatProgressSpinnerModule,

    NgxCurrencyModule.forRoot(customCurrencyMaskConfig),
  ],
  providers: [
    ApplicantInformationService,
    ReferencesService,
    AgencySelectionService,
    FacialRecognitionService,
  ],
})
export class BoardingModule {}
