import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { CodigoActivacionResponse } from 'src/app/core/boarding/_models/codigoactivacionResponse.model';
import { ValidarAspiranteResponseModel } from 'src/app/core/boarding/_models/validarAspiranteResponse.model';
import { ReferencesService } from 'src/app/core/boarding/_services/references.service';
import { SuccessComponent } from '../success/success.component';

@Component({
  selector: 'app-contract',
  templateUrl: './contract.component.html',
  styles: [
  ]
})
export class ContractComponent implements OnInit {

  claveTemp: string;
  inputClaveTemp = true;
  keyboardFlag: boolean;
  formulario: FormGroup;
  alertaError = true;
  msgAlertaError: string;
  btnContinuar = true;
  alertaCodigoActivacion = true;
  validaAspirante: ValidarAspiranteResponseModel;
  codigoActivacion: CodigoActivacionResponse;
  numeroCliente: string;
  correoCliente: string;
  checkClass = false;
  body = {
    Cliente: "POSTMAN",
    Token: "123",
    IdAcceso: "10.150.0.142",
    UsuarioFinal: "PRUEBA LOCAL"
  };

  constructor(
    public dialog: MatDialog,
    public dialogRef: MatDialogRef<ContractComponent>,
    private fb: FormBuilder,
    private referenceService: ReferencesService
  ) {
    this.formulario = fb.group({
      checkTerminos: []
    });
  }

  ngOnInit(): void {
    this.claveTemp = '';
    this.keyboardFlag = true;
    this.validaAspirante = JSON.parse(sessionStorage.getItem('ValidaAspirante'));
    this.numeroCliente = this.validaAspirante.DatosAspirante.Celular;
    this.correoCliente = this.validaAspirante.DatosAspirante.CorreoElectronico;        
  }

  maskClientNumber() {
    const first2 = this.numeroCliente.substring(0, 2);
    const last2 = this.numeroCliente.substring(this.numeroCliente.length - 2);
    const mask = this.numeroCliente.substring(2, this.numeroCliente.length - 2).replace(/\d/g, "*");
    const clientNumber = first2 + mask + last2;
    return clientNumber;
  }

  maskClientEmail() {    
    const arrayClientEmail = this.correoCliente.split('@');
    const length = Math.round(arrayClientEmail[0].length / 2);
    if (length > 1) {
      const firstEmail = this.correoCliente.substring(0, length);
      const mask = this.correoCliente.substring(length, arrayClientEmail[0].length).replace(/./g, "*");
      return firstEmail + mask + '@' + arrayClientEmail[1];
    }
    return this.correoCliente;
  }

  keyboard(value: string) {
    if(this.claveTemp.length === 6){
      return;
    }
    this.claveTemp = this.claveTemp + value;
  }

  backSpace() {
    this.claveTemp = this.claveTemp.substring(0, this.claveTemp.length - 1);
  }

  focusEvent() {
    this.keyboardFlag = false;
  }

  success() {
    if (this.formulario.get('checkTerminos').value) {
      this.alertaError = true;
    } else {
      this.alertaError = false;
      this.msgAlertaError = 'Contrato no se ha aceptado por Cliente';
      return;
    }
    this.dialogRef.close();
    const dialogRef = this.dialog.open(SuccessComponent, { data: {} });
    dialogRef.afterClosed().subscribe(res => {
      if (!res) {
        return;
      }
    });
  }

  close(): void {
    this.dialogRef.close();
  }

  cerrarAlertaError() {
    this.alertaError = true;
  }

  async obtenerCodigoActivacion() {
    if (this.formulario.get('checkTerminos').value) {
      this.checkClass = false;
      this.alertaError = true;
      this.alertaCodigoActivacion = false;
      this.inputClaveTemp = false;
      this.btnContinuar = false;
      const body = {
        Auditoria: this.body,
        Celular: this.numeroCliente,
        Correo: this.correoCliente
      }
      this.codigoActivacion = await this.referenceService.ConsultaCodigoActivacion(body).toPromise();
      console.log(this.codigoActivacion.Referencia);
    } else {
      this.checkClass = true;
      this.msgAlertaError = 'Contrato no se ha aceptado por Cliente';
      this.alertaError = false;      
    }  
  }
}
