import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Injectable } from '@angular/core';
import { BackReplyModel } from '../_models/back-reply.model';

@Injectable()
export class ApplicantInformationService {
  private readonly apiUrl = 'http://192.168.33.12:81';

  private readonly civilStatus: string = '/api/ConEstadosCivil';
  private readonly provincias: string = '/api/ConProvincias';
  private readonly cantones: string = '/api/ConCantones';
  private readonly parroquias: string = '/api/ConParroquias';
  private readonly rlaborales: string = '/api/ConRelacionesLaboral';
  private readonly aeconomicas: string = '/api/ConActividadesEconomicas';
  private readonly foingresos: string = '/api/ConFuentesOtrosIngresos';
  private readonly conPaises: string = '/api/ConPaises';
  private readonly tvivienda: string = '/api/ConTiposVivienda';

  constructor(private http: HttpClient) {}

  private get urlCivilStatus() {
    return this.apiUrl + this.civilStatus;
  }
  private get urlProvincias() {
    return this.apiUrl + this.provincias;
  }
  private get urlCantones() {
    return this.apiUrl + this.cantones;
  }
  private get urlParroquia() {
    return this.apiUrl + this.parroquias;
  }
  private get urlrLaborales() {
    return this.apiUrl + this.rlaborales;
  }
  private get urlaEconomicas() {
    return this.apiUrl + this.aeconomicas;
  }
  private get urlfoIngresos() {
    return this.apiUrl + this.foingresos;
  }
  private get urlconPaises() {
    return this.apiUrl + this.conPaises;
  }
  private get urltVivienda() {
    return this.apiUrl + this.tvivienda;
  }

  ConsultaCivilStatus(body: any): Observable<BackReplyModel> {
    const endpointUrl = `${this.urlCivilStatus}`;
    return this.http.post<BackReplyModel>(endpointUrl, body);
  }
  ConsultaProvincias(body: any): Observable<BackReplyModel> {
    const endpointUrl = `${this.urlProvincias}`;
    return this.http.post<BackReplyModel>(endpointUrl, body);
  }
  ConsultaCantones(body: any): Observable<BackReplyModel> {
    const endpointUrl = `${this.urlCantones}`;
    return this.http.post<BackReplyModel>(endpointUrl, body);
  }
  ConsultaParroquia(body: any): Observable<BackReplyModel> {
    const endpointUrl = `${this.urlParroquia}`;
    return this.http.post<BackReplyModel>(endpointUrl, body);
  }
  ConsultaRLaboral(body: any): Observable<BackReplyModel> {
    const endpointUrl = `${this.urlrLaborales}`;
    return this.http.post<BackReplyModel>(endpointUrl, body);
  }
  ConsultaAEconomicas(body: any): Observable<BackReplyModel> {
    const endpointUrl = `${this.urlaEconomicas}`;
    return this.http.post<BackReplyModel>(endpointUrl, body);
  }
  ConsultaFOIngresos(body: any): Observable<BackReplyModel> {
    const endpointUrl = `${this.urlfoIngresos}`;
    return this.http.post<BackReplyModel>(endpointUrl, body);
  }
  ConsultaconPaises(body: any): Observable<BackReplyModel> {
    const endpointUrl = `${this.urlconPaises}`;
    return this.http.post<BackReplyModel>(endpointUrl, body);
  }
  ConsultaTVivienda(body: any): Observable<BackReplyModel> {
    const endpointUrl = `${this.urltVivienda}`;
    return this.http.post<BackReplyModel>(endpointUrl, body);
  }
}
