import { Component, OnInit } from '@angular/core';
import { FormControl } from '@angular/forms';
import { DatosAspiranteModel } from '../../../core/boarding/_models/datos-aspirante.model';
import { DomicilioModel } from '../../../core/boarding/_models/domicilio.model';
import { InformacionEconomicaModel } from '../../../core/boarding/_models/informacion-economica.model';
import { ConyugeModel } from '../../../core/boarding/_models/conyuge.model';
import { FotoModel } from '../../../core/boarding/_models/foto.model';
import { ResidenciaFiscalExtranjeroModel } from '../../../core/boarding/_models/residencia-fiscal-extranjero.model';
import { EntregaTarjetaModel } from '../../../core/boarding/_models/entrega-tarjeta.model';
import { ActivatedRoute } from '@angular/router';
import {ParametrosInicialesResponse} from '../../../core/boarding/_models/parametros-inicialesResponse.model';
import {AuditService} from '../../../core/_base/services/audit.service';
import {ParametersService} from '../../../core/boarding/_services/parameters.service';

@Component({
  selector: 'app-boarding',
  templateUrl: './boarding.component.html',
  styleUrls: ['./boarding.component.scss'],
})
export class BoardingComponent implements OnInit {
  selected = new FormControl(0);
  cantidadReferencias = 0;
  EsCliente = false;
  aspirante: DatosAspiranteModel;

  parametrosIniciales: ParametrosInicialesResponse;
  numberPersonalRef: number;
  numberBankRef: number;

  constructor(private route: ActivatedRoute,
              public auditService: AuditService,
              private paramsService: ParametersService) {}

  ngOnInit(): void {
    this.route.data.subscribe((data) => {
      this.aspirante = data.info.DatosAspirante;
      this.EsCliente = data.info.EsCliente;
    });

    this.paramsService.GetInitialParameters(this.auditService.getAudit()).subscribe(
      next => {
        this.parametrosIniciales = next;
        if(next.MinimoReferenciasBancarias > 0 || next.MinimoReferenciasPersonales > 0){
          this.cantidadReferencias = 1;
        }
      }
    );
  }

  getInfoAspirante() {
    return this.aspirante;
  }

  siEsCliente() {
    return this.EsCliente;
  }

  nextTab() {
    if (this.selected.value < 4) {
      this.selected.setValue(this.selected.value + 1);
    }
  }

  prevTab() {
    if (this.selected.value > 0) {
      this.selected.setValue(this.selected.value - 1);
    }
  }

  setDomicilio(a: DomicilioModel) {
    this.aspirante.Domicilio = a;
  }

  setEntregaTarjeta(a: EntregaTarjetaModel) {
    this.aspirante.EntregaTarjeta = a;
  }

  setConyuge(a: ConyugeModel) {
    this.aspirante.Conyuge = a;
  }

  setFotos(a: FotoModel[]) {
    this.aspirante.Fotos = a;
  }

  tieneRecidenciaFiscalEnElExtranjero(a: boolean) {
    this.aspirante.TieneResidFiscalExtranjero = a;
  }

  TieneCuentasSistemaFinancieroNacional(a: boolean) {
    this.aspirante.TieneCuentasSistemaFinancieroNacional = a;
  }

  setInformacionEconomica(a: InformacionEconomicaModel) {
    this.aspirante.InformacionEconomica = a;
  }

  setReferenciasPersonales(a) {
    this.aspirante.ReferenciasPersonales = a;
  }

  setReferenciasBancarias(a) {
    this.aspirante.ReferenciasBancarias = a;
  }
}
